import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

// Firebase imports
import { AngularFireDatabase } from 'angularfire2/database';
import { FirebaseListObservable } from 'angularfire2/database';

import { Job } from '../../models/interfaces/job.interface';

/**
 * Generated class for the JobCreationPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-job-creation',
  templateUrl: 'job-creation.html',
})
export class JobCreationPage {

  job = {} as Job;
  jobRef$: FirebaseListObservable<Job[]>

  constructor(private navCtrl: NavController, private navParams: NavParams, private database: AngularFireDatabase) {
    this.jobRef$ = database.list('job-list');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad JobCreationPage');
  }

  addJob(job: Job){

    this.jobRef$.push({
      _id: this.job.id,
      _name: this.job.name,
      _address: this.job.address

    })

    //Reset our job
    this.job = {} as Job;

    // Navigate the user back to the Job List
    this.navCtrl.pop();

  }

}



